
import { reactive, toRefs, onMounted, getCurrentInstance } from "vue"
export default {
  name: "Address",
  props: {
    title: String,
    leftShow: {
      type: Boolean,
      default: false,
    },
  },
  setup() {
    const { proxy }: any = getCurrentInstance()
    const state = reactive<any>({
      showPopup: false,
      addressValue: [],
      addressText: "",
    })

    onMounted(() => {
      getAddressData()
    })

    /** 获取地址列表 */
    const getAddressData = () => {
      proxy.$axios
        .get("getAddress")
        .then((res: any) => {
          //请求成功
          console.log(res)
          address.province = res.province
        })
        .catch((err: any) => {
          console.log(err)
        })
    }

    const showAddress = () => {
      state.showPopup = !state.showPopup
    }
    const address = reactive<any>({
      province: [],
      city: [],
      country: [],
      town: [],
      placeholder: "请选择",
    })

    const onChange = (cal: any) => {
      console.log(cal)
      address[cal.next] = cal.value[cal.next]
      const name = address[cal.next]
      if (name.length < 1) {
        state.showPopup = false
      }
    }

    const close = (val: any) => {
      console.log(val)
      state.addressText = val.data.addressStr
      state.addressValue = [
        val.data.province.id,
        val.data.city.id,
        val.data.country.id,
        val.data.town.id,
      ]
    }

    const closeMask = (evt: any) => {
      address.city = []
      address.country = []
      address.town = []
      console.log(evt)
    }

    return {
      ...toRefs(state),
      ...toRefs(address),
      showAddress,
      onChange,
      close,
      closeMask,
    }
  },
}
